import { FC, PropsWithChildren, forwardRef } from 'react';
import { styled } from '../../utils';
import { TwBaseProps } from '../../types';
import {
  Table as MantineTable,
  TableProps as MantineTableProps,
} from '@mantine/core';

export interface TableProps
  extends TwBaseProps,
    PropsWithChildren,
    Omit<MantineTableProps, 'DefaultProps'> {}

const Root = styled(MantineTable)<MantineTableProps>(() => ({}));

export const Table: FC<TableProps> = forwardRef((props, ref) => {
  return <Root {...props} ref={ref} />;
});
